@charset "UTF-8";
/* CSS Document */
/* 清除内外边距 */
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote,
dl, dt, dd, ul, ol, li,
pre,
fieldset, lengend, button, input, textarea,
th, td {
  /* table elements 表格元素 */
  margin: 0;
  padding: 0; }

/* 设置默认字体 */
body,
button, input, select, textarea {
  /* for ie */
  font: 12px/1  "微软雅黑",Arial,Tahoma, Helvetica, "\5b8b\4f53", sans-serif;
  /* 用 ascii 字符表示，使得在任何编码下都无问题 */ }

body {
  position: relative; }

address, cite, dfn, em, var, i {
  font-style: normal; }

/* 将斜体扶正 */
/* 重置列表元素 */
ul, ol {
  list-style: none; }

/* 重置文本格式元素 */
a {
  text-decoration: none; }

a:hover {
  text-decoration: underline;
  transition: all 0.5s ease 0s;
  color: #0B7DF2; }

/* 去除a标签点击后的虚线框 */
a:focus {
  outline: none;
  text-decoration: none;
  -moz-outline: none; }

/* 重置表单元素 */
button, input, select, textarea {
  font-size: 100%;
  /* 使得表单元素在 ie 下能继承字体大小 */ }

input {
  outline: none; }

/* 重置表格元素 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

/* 重置 hr */
hr {
  border: none;
  height: 1px; }

/*设置textarea固定大小*/
textarea {
  resize: none; }

img {
  vertical-align: middle; }

* {
  box-sizing: border-box; }

